.login-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: 'content';
  margin-top: 20vh;
  border: red 1px solid;
}

.login-inner > button {
  width: 100%;
  height: 2rem;
  background-color: transparent;
  border-radius: 1rem;
  border: rgba(165, 165, 165, 0.5) 0.1rem solid;
}

@media only screen and (min-width: 350px) {
  .login-inner {
    margin-left: 25%;
    margin-right: 25%;
  }
}

.login-inner {
  grid-area: content;
  padding: 2rem;
  border: blue 1px solid;
}

.field > input {
  display: block;
  width: 100%;
  height: 2rem;
  border-radius: 0.3rem;
  border: rgba(165, 165, 165, 0.5) 0.1rem solid;
  margin-bottom: 1rem;
}



